.dc-btn {
  display: inline-flex;
  max-width: 100%;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  font-family: "Montserrat", serif;
  vertical-align: middle;
  color: #fff;
  font-weight: 400;
  text-align: center;
  user-select: none;
  background-color: transparent;
  padding: 25px 40px;
  font-size: 18px;
  letter-spacing: 1.26px;
  line-height: 1;
  border-radius: 0px;
  text-decoration: none;
  position: relative;
  border: 1px solid transparent;
  outline: 0;
}
@media (max-width: 991.98px) {
  .dc-btn {
    padding: 6px 9px;
    font-size: 5px;
  }
}
@media screen and (max-width: 480px) {
  .dc-btn {
    padding: 11px 15px;
    font-size: 11px;
  }
}
.dc-btn_primary {
  color: #4D4D4D;
  border-radius: 24px;
  background-color: #6222cc;
  border-color: #6222cc;
  transition: all 0.4s ease;
}
@media (min-width: 1400px) {
  .dc-btn_primary {
    font-size: 1em;
    padding-top: 1em;
    padding-bottom: 1em;
    padding-left: 3.875em;
    padding-right: 3.875em;
  }
}
.dc-btn_primary:hover {
  background-color: #4f1ba5;
  border-color: #4f1ba5;
  color: var(--text-color);
}
.dc-btn_secondary {
  color: #fff;
  background-color: #29CFCF;
  border-color: #29CFCF;
  transition: all 0.4s ease;
}
.dc-btn_secondary:hover {
  background-color: #25baba;
  border-color: #25baba;
}
.dc-btn_outline-primary {
  text-transform: uppercase;
  border-color: #0F0F0F;
  color: #0F0F0F;
  transition: all 0.4s ease;
}
.dc-btn_outline-primary:hover {
  background-color: #0F0F0F;
  color: #fff;
}
.dc-btn_outline-white {
  border-color: #fff;
  color: #fff;
  transition: all 0.4s ease;
}
.dc-btn_outline-white:hover {
  background-color: #fff;
  color: #0F0F0F;
}
.dc-btn_outline-secondary {
  border-color: #6222cc;
  color: #6222cc;
  transition: all 0.4s ease;
}
.dc-btn_outline-secondary:hover {
  background-color: #6222cc;
  color: #fff;
}
.dc-btn_gradient {
  overflow: hidden;
  color: #fff;
  border: none;
  background-color: #3f70d5;
  background-image: linear-gradient(to right, #3f70d5 0%, #7fbc51 100%);
  background-repeat: repeat-y;
  background-position: 150px 0;
  transition: background-position 0.2s ease;
}
.dc-btn_gradient:hover {
  background-position: 0px 0;
}
.dc-btn_loading {
  color: transparent !important;
}
.dc-btn_loading:after {
  animation: spinAround 0.5s infinite linear;
  border: 3px solid #dbdbdb;
  border-radius: 290486px;
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 16px;
  position: relative;
  width: 16px;
  position: absolute;
  left: calc(50% - (16px / 2));
  top: calc(50% - (16px / 2));
  position: absolute !important;
}
.dc-btn:disabled {
  background-color: #ddd !important;
  border-color: #ddd !important;
  cursor: default !important;
  box-shadow: none !important;
}
.dc-btn_lg {
  padding: 10px 25px;
  font-size: 16px;
}
.dc-btn_sm {
  padding: 5px 15px;
  font-size: 12px;
}
.dc-btn_block {
  display: flex;
  width: 100%;
}
.dc-btn_shadow {
  box-shadow: 0px 10px 15px 0px rgba(27, 95, 193, 0.19);
}
